.products__name {
    font-family: var(--title-font);
    font-weight: 400;
    font-size: 18px;
    line-height: 21px;
    text-align: center;
    text-transform: uppercase;
    color: var(--pretitle-text-color);

    @include for-tablet-up {
        font-size: 26px;
        line-height: 30px;
    }

    @include for-desktop-up {
        font-size: 30px;
        line-height: 34px;
    }
}

.products__title {
    font-family: var(--title-font);
    font-weight: 400;
    font-size: 34px;
    line-height: 39px;
    text-align: center;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    color: var(--title-text-color);
    margin-top: 15px;

    @include for-tablet-up {
        font-size: 48px;
        line-height: 55px;
    }

    @include for-desktop-up {
        font-size: 58px;
        line-height: 66px;
    }
}

.products__item {


    @include for-tablet-up {
        display: flex;
        flex-wrap: wrap;
        gap: 16px;
    }

    @include for-desktop-up {
        gap: 32px;
    }
}

.products__list {
    position: relative;

    width: 280px;
    height: 489px;
    border-radius: 24px;
    margin-top: 140px;
    margin-left: auto;
    margin-right: auto;


    @include for-tablet-up {
        width: 224px;
        height: 473px;
        margin-top: 158px;
    }

    @include for-desktop-up {
        width: 368px;
        height: 535px;
        margin-top: 280px;
    }

}


.ice-cream-p {
    background-color: #FFB8CA;

    &::before {
        position: absolute;
        content: " ";
        display: block;
        width: 280px;
        height: 280px;
        top: -29%;
        left: 0;
        background-image: url(../images/mobile/products-ice-cream@1x.png);

        @include for-pixel-ratio-2 {
            background-image: url(../images/mobile/products-ice-cream@2x.png);
        }


        @include for-tablet-up {
            width: 224px;
            height: 224px;
            background-image: url(../images/tablet/products-ice-coffee@1x.png);

            @include for-pixel-ratio-2 {
                background-image: url(../images/tablet/products-ice-coffee@2x.png);
            }
        }

        @include for-desktop-up {
            width: 368px;
            height: 394px;
            top: -49%;
            background-image: url(../images/desktop/products-ice-cream@1x.png);

            @include for-pixel-ratio-2 {
                background-image: url(../images/desktop/products-ice-cream@2x.png);
            }


        }
    }
}

.ice-coffee {
    background-color: #F0D1A5;

    &::before {
        position: absolute;
        content: " ";
        display: block;
        width: 280px;
        height: 280px;
        top: -29%;
        left: 0;
        background-image: url(../images/mobile/products-ice-coffee@1x.png);

        @include for-pixel-ratio-2 {
            background-image: url(../images/mobile/products-ice-coffee@2x.png);
        }

        @include for-tablet-up {
            width: 224px;
            height: 224px;
            background-image: url(../images/tablet/products-ice-cream@1x.png);

            @include for-pixel-ratio-2 {
                background-image: url(../images/tablet/products-ice-cream@2x.png);
            }
        }

        @include for-desktop-up {
            width: 368px;
            height: 394px;
            top: -49%;
            background-image: url(../images/desktop/products-ice-coffee@1x.png);

            @include for-pixel-ratio-2 {
                background-image: url(../images/desktop/products-ice-coffee@2x.png);
            }
        }
    }
}

.milkshakes {
    background-color: #C2E297;

    &::before {
        position: absolute;
        content: " ";
        display: block;
        width: 280px;
        height: 280px;
        top: -29%;
        left: 0;
        background-image: url(../images/mobile/products-ice-milkshakes@1x.png);

        @include for-pixel-ratio-2 {
            background-image: url(../images/mobile/products-ice-milkshakes@2x.png);
        }

        @include for-tablet-up {
            width: 224px;
            height: 224px;
            background-image: url(../images/tablet/products-ice-milkshakes@1x.png);

            @include for-pixel-ratio-2 {
                background-image: url(../images/tablet/products-ice-milkshakes@2x.png);
            }
        }

        @include for-desktop-up {
            width: 368px;
            height: 394px;
            top: -49%;
            background-image: url(../images/desktop/products-ice-milkshakes@1x.png);

            @include for-pixel-ratio-2 {
                background-image: url(../images/desktop/products-ice-milkshakes@2x.png);
            }
        }
    }
}

.products__pretitle {
    color: var(--white-text-color);
    font-family: var(--title-font);
    font-weight: 400;
    font-size: 20px;
    line-height: 23px;
    text-align: center;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    padding-top: 160px;

    &::after {
        content: "...";
        display: flex;
        align-items: center;
        margin: 0 auto;
        width: 22px;

        margin-top: 9px;

        @include for-desktop-up {
            margin-top: 0;
        }
    }

    @include for-desktop-up {
        font-size: 30px;
        line-height: 34px;
    }
}

.products__text {
    font-weight: 700;
    font-size: 14px;
    line-height: 152%;
    text-align: center;
    letter-spacing: 0.04em;
    color: var(--white-text-color);
    width: 200px;

    @include for-desktop-up {
        font-size: 16px;
        line-height: 1.76;
        width: 245px;
    }

    margin: 0 auto;
    display: block;
    padding-top: 32px;
    padding-bottom: 32px;
}

.products__btn {
    margin: 0 auto;
    display: block;
    position: relative;
    border-radius: 50%;
    width: 40px;
    height: 40px;
    margin-top: 32px;
    // transition: transform 0.2s var(--time-function), background-color 0.2s var(--time-function), box-shadow 0.2s var(--time-function);
    transition: transform 0.2s var(--time-function), background-color 0.2s var(--time-function);
}

.products__btn:hover,
.products__btn:focus {
    background-color: var(--title-text-color);
    transform: scale(1.02);
    // box-shadow: 2px 2px 4px rgb(10 10 10 / 10%);

    &::before,
    &::after,
    .button__icon {
        background-color: var(--white-text-color);
    }
}


.circle-button__p {
    fill: currentColor;
}

.products-btn {
    margin: auto;
}

