.close-2 {
    display: flex;
    justify-content: center;
    align-items: center;
    bottom: 40px;

    @include for-desktop-up {
        bottom: 60px;
    }

    left: 50%;
    transform: translateX(-50%);
    width: 40px;
    height: 40px;
    position: absolute;
    border: none;
    border-radius: 50%;
    background-color: var(--title-text-color);
    box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16),
    0 2px 10px 0 rgba(0, 0, 0, 0.12);
    cursor: pointer;
    outline: none;
}

.close-2:before {
    color: #fff;
    content: "X";
    font-family: Arial, Helvetica, sans-serif;
    font-size: 14px;
    font-weight: normal;
    text-decoration: none;
    text-shadow: 0 -1px rgba(0, 0, 0, 0.9);
}

.close-2:hover {
    background-color: var(--title-text-color);
}

#overlay .popup p.zag,
#overlay2 .popup p.zag,
#overlay3 .popup p.zag {
    margin: 20px 0 10px;
    padding: 0 0 6px;
    color: tomato;
    font-size: 16px;
    font-weight: bold;
    border-bottom: 1px solid tomato;
}

.portfolio-list-overlay {
    position: relative;
    overflow: hidden;
}

.milkshakes:hover .portfolio-overlay {
    transform: translateY(0);
}

.portfolio-overlay {
    position: absolute;
    bottom: 0;
    height: 70%;
    font-style: normal;
    font-size: 18px;
    line-height: 1.56;
    letter-spacing: 0.03em;
    color: #FFFFFF;
    padding: 10px;
    line-height: 156%;
    background-color: #c2e297;
    transform: translateY(100%);
    transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
    border-radius: 24px;
    text-transform: uppercase;
    text-align: center;
    box-shadow: rgba(0, 0, 0, 0.4);


}

.ice-coffee:hover .portfolio-overlay {
    transform: translateY(0);
}

.ice-coffee .popup {
    background-color: #F0D1A5;

}

.milkshakes .popup {
    background-color: #C2E297;
}

